<template>
    <div id="Signin">
        <Header2  :FlagSignin=true :FlagAcc=false :FlagEdit=false  :FlagAdded=false />
        
        <div class="content">
            <div class="con">
                <van-cell-group>
                <van-field
                    v-model="username"
                    clearable
                    label="账号"
                    placeholder="请输入账号"/>
                <van-field
                    v-model="password"
                    type="password"
                    label="密码"
                    placeholder="请输入密码"/>
                <van-switch v-model="checked"  size="24px" class="van"/>
                <van-field
                    v-model="captcha_code"
                    clearable
                    label="验证码"
                    placeholder="请输入密码"/>
                    
                    <div class="cont">
                        <div class="cont1"><img  :src="code" alt="" /></div>
                        <div class="cont2" @click="changeImg()">
                            <div class="cont2-1">看不清</div>
                            <div class="cont2-2">换一张</div>
                        </div>
                    </div>
                </van-cell-group>
            </div>
            <div class="con1">
                <div class="con1-1">温馨提示：未注册过得账号，登录时将自动注册</div>
                <div class="con1-2">注册过的用户可凭账号密码登录</div>
                <button class="btn" @click="denglu()">登录</button>
                <div class="con1-3">重置密码？</div>
            </div>
        </div>
    </div>
</template>

<script>
import Header2 from '../components/Header2'
import axios from 'axios'
import Vue from 'vue';
import { Dialog } from 'vant';
axios.defaults.withCredentials=true;

export default {
    components:{
        Header2,
        [Dialog.Component.name]: Dialog.Component
    },
    name: 'HelloWorld',
    data () {
        return {
            checked: true,
            username:'',
            password:'',
            captcha_code:'',
            code:'',
        }
    },
    created(){
        
    },
    methods:{
        denglu(){
            axios.post(`http://elm.cangdu.org/v2/login`,
            {captcha_code:this.captcha_code,
             password:this.password,
             username:this.username
            }).then(res=>{
                console.log(res)
                if(res.data.status==0){
                   if(this.captcha_code!==''&&this.password!==''&&this.username!==''){
                    Dialog.alert({
                        message:'密码错误'
                    })
                }
                }else{
                    // localStorage.setItem('user_id',JSON.stringify(this.captcha_code))
                    this.$store.commit('key',res.data)
                    this.$router.push('/Homepage')
                }  
               
                if(this.captcha_code==''&&this.password==''&&this.username==''){
                    Dialog.alert({
                        message:'请输入手机号/邮箱/用户名'
                    })
                }
            })
        },
        changeImg(){
            axios.post(`http://elm.cangdu.org/v1/captchas`,{}).then(res=>{
                if(res.data.code !=''){
                    this.code=res.data.code
                }
            })
        }
    },
    mounted(){
        this.changeImg()
    }
}
</script>
<style lang="scss">
#Signin{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F5F5F5;
    position: fixed;
    top: 0;
    // background: #fff;
    z-index:5;
    display: flex;
    .content{
        margin-top: 2rem;
        .con{
             margin-top: 0.4rem;
             position: relative;
             .van-cell{
                 line-height: 1.5rem;                       
             }
             .van{
                 position: absolute;
                 top:3rem;
                 right: 1rem;
             }
             .cont{
                 position: absolute;
                 top: 5rem;
                 right: 1rem;
                 display: flex;
                 .cont1{
                     margin-right: 0.3rem;
                 }
                 .cont2{
                     font-size: 0.5rem;
                     .cont2-2{
                         color: #3b95e9;
                     }
                 }
             }
        }
        .con1{
            margin-top: 0.6rem;
            position: relative;
            .con1-1{
                margin-left: 5%;
                color: red;
                font-size: 0.5rem;
            }
            .con1-2{
                margin-left: 5%;
                color: red;
                margin-top: 0.6rem;
                font-size: 0.5rem;
            }
            .btn{
                margin-top: 0.6rem;
                width: 90%;
                margin-left: 5%;
                background: #4cd964;
                color: white;
                line-height: 2rem;
                border-radius: 0.1rem;
                border: none;
                outline-style: none ;
            }
            .con1-3{
                position: absolute;
                top:6rem;
                right: 1rem;
                color: #3b95e9;
            }
        }
    }
}
    
</style>



